h1, h2, h3, h4, ul, li, button{
    color: var(--font-color);
    text-transform: uppercase;
    letter-spacing: 1px;
    white-space: nowrap;
    text-decoration: none;
    font-style: normal;
    border: none;
    background-color: transparent;
    margin: 0;
    transition: 0.3s ease-in-out;
}

h1{
    font-size: 32px;
    margin-bottom:15px;
}

h4{
    font-size: 16px;
    margin-bottom: 10px;
    border-bottom: 1px solid var(--font-color);
    color: #a6adb4;
}

h6{
    margin: 0px;
}

ul{
    padding-left: 0px;
}

.intro-h4{
    border-bottom: 0px;
}

button{
    color: var(--font-color);
    background: transparent;
    border: none;
    font-weight: 600;
}

.button{
    margin: 10px;
    justify-self: left;
    margin-left: 0px;
    padding-left: 0px;
}
.buttonC, .button{
    text-transform: uppercase;
    letter-spacing: 1px;
    white-space: nowrap;
    color: var(--font-color);
    font-weight: 100;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    &:hover {
        transform: translateY(-3px);
        color: #CF3F3A;
    }
}

.boxW, .boxB, .boxR {
    display: inline-block;
    width: 10px; 
    height: 10px; 
    margin-right: 10px; 
    cursor: pointer;
    border: 1px solid var(--font-color);
}

.boxW{background-color: #F4F6F6; }
.boxB {background-color: #1A1E23; }

.sidePanel{
    position: fixed;
    display:flex;
    bottom: 0;
    width: 40px;
    padding: 15px;
    padding-left: 0;
    ul{
        padding-left: 0;
        padding-right: 40px;
    }
}

.links{
    margin-top: 30px;
    position: relative;
    display:grid;
    justify-content: start;
}

.page{
    position: relative;
    height: 100vh; 
    width: auto;
}

.display{
    position: relative;
    border: 3px solid black;
    height: 500px;
    width: 800px;
    overflow: visible;
}

.artex-box{
    width: auto;
    height: 650px;
    overflow: hidden;
}

.projectUL{
    position: relative;
    display: flex;
}

.store{
    list-style: none;
    margin: 40px;
}